/* Header  */

.header {

  &__top {
    background-color: #f5f6f6;
    padding: 8px 19px;
    border-bottom: 1px solid rgb(209, 211, 212);

    ul {
      display: flex;
      flex-direction: row;
      line-height: 1;
      list-style: none;
      padding: 0;
      margin: 0;

      li {
        a {
          color: #6d6e71;
          text-decoration: none;
          transition: all 0.5s ease;
          font-size: 13px;
          font-family: "Century Gothic", sans-serif;

          &:hover {
            color: $link_color_active;
            text-decoration: underline;
          }
        }

        &.active {
          a {
            color: $link_color_active;
            text-decoration: underline;
          }
        }

        &:not(:last-child) {
          margin-right: 40px;
        }
      }
    }

    &-menu {
      justify-content: flex-start;
    }

    &-menu-account {
      justify-content: flex-end;
      letter-spacing: 0.1px;

      li:nth-child(1) {
        border-right: 1px solid #d1d3d4;
        margin-right: 0 !important;
        padding-right: 15px;
      }

      li:nth-child(2) {
        padding: 0 8px;
      }

      .module {

        &__login {
          position: relative;
          padding-left: 30px;
          &:before {
            content: '';
            display: block;
            position: absolute;
            left: 0;
            top: 50%;
            margin-top: -9px;
            height: 18px;
            width: 16px;
            background-image: url("data:image/svg+xml;base64,");
            background-position: center left;
            background-repeat: no-repeat;
            background-size: 17px;
          }
        }

        &__language {

          a {
            color: #231f20;
          }
        }
      }

    }

  }

  &__middle {
    padding: 14px 0;

    .header__last-block {
      text-align: right;
      padding-right: 45px;
    }

  }

  &__menu {

  }

}

.main__menu {
  display: flex;
  flex-direction: row;
  border-bottom: 1px solid #d1d3d4;
  border-top: 1px solid #d1d3d4;
  padding: 0 40px;

  li {
    border-bottom: 2px solid transparent;
    transition: all 0.5s ease;

    a {
      font-size: 22px;
      font-family: "Century Gothic", sans-serif;
      color: #6d6e71;
      line-height: 1;
      text-align: center;
      text-decoration: none;
      padding: 13px 25px;
      display: block;
      transition: all 0.5s ease;
    }

    &.active, .curent-page-item, &:hover, &:focus {
      border-bottom: 2px solid #f4782c;

      a {
        color: rgb(245, 127, 41);
      }
    }
  }
}

.service__menu {

  &-item {
    font-size: 12px;
    font-family: "Century Gothic", sans-serif;
    color: rgb(109, 110, 113);
    padding: 9px 0 9px 35px;
    position: relative;
    line-height: 1;

    &:not(:last-child) {
      margin-bottom: 8px;
    }

    &:before {
      content: '';
      display: block;
      position: absolute;
      left: 0;
      top: 50%;
      margin-top: -11px;
      height: 22px;
      width: 18px;
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
    }

  }

  &-location {
    &:before {
      background-image: url("data:image/svg+xml;base64,");
    }
  }

  &-gift {
    &:before {
      background-image: url("data:image/svg+xml;base64,");
    }
  }

  &-search {
    padding-right: 40px;

    &:before {
      background-image: url("data:image/svg+xml;base64,");
    }

    input {
      width: 100%;
      border: 0;
      border-bottom: 1px solid #bcbec0;
      outline: 0;
      color: rgb(109, 110, 113);
      max-width: 235px;
    }
  }
}

.contacts {

  &__item {
    padding: 9px 0 9px 35px;
    position: relative;
    line-height: 1;

    &:not(:last-child) {
      margin-bottom: 10px;
    }

    &:before {
      content: '';
      display: block;
      position: absolute;
      left: 0;
      top: 50%;
      margin-top: -11px;
      height: 22px;
      width: 18px;
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
    }

    a, button {
      font-size: 12px;
      font-family: "Century Gothic", sans-serif;
      color: rgb(109, 110, 113);
      font-weight: 400;
      text-decoration: none;
    }
  }

  &__phone {
    &:before {
      background-image: url("data:image/svg+xml;base64,");
    }
  }

  &__mail {
    &:before {
      background-image: url("data:image/svg+xml;base64,");
    }
  }

  &__button {
    padding: 0;
  }

}

.button__callBack {
  border-width: 1px;
  border-color: rgb(188, 190, 192);
  border-style: solid;
  border-radius: 4px;
  width: 100%;
  max-width: 170px;
  height: 28px;
  line-height: 28px;
  padding: 0;
  text-align: center;
  background-color: transparent;
  cursor: pointer;
  outline: 0;

  svg {
    vertical-align: text-bottom;
    margin-right: 5px;
  }

  &:hover {
    color: #f57f29;
    border: 1px solid #f57f29;

    svg {
      path {
        fill: #f57f29!important;
      }
    }
  }
}


.cart {
  border-color: #231f20;
  a {
    margin-left: 15px;
    border-width: 1px;
    border-color: #231f20;
    color: #231f20;
    border-style: solid;
    border-radius: 4px;
    width: 100%;
    max-width: 170px;
    height: 28px;
    line-height: 28px;
    padding: 4px 10px;
    text-align: center;
    background-color: transparent;
    cursor: pointer;
    text-decoration: none;
  }
}

.socials {
  margin-top: 23px;

  &__header {
    line-height: 1;
    margin-bottom: 15px;
    font-size: 14px;
    font-family: "Century Gothic", sans-serif;
    color: rgb(109, 110, 113);
  }

  ul {
    padding: 0;
    margin: 0;
    list-style: none;

    li {
      display: inline-block;

      &:not(:last-child) {
        margin-right: 4px;
      }

      a {
        display: block;
        width: 25px;
        height: 25px;
        border-radius: 50%;
        border: 1px solid #6d6e71;
        text-align: center;
        line-height: 25px;
        color: rgb(109, 110, 113);
        transition: all 0.5s ease;

        &:hover {
          color: #f57f29;
          border: 1px solid #f57f29;
        }
      }
    }
  }
}