/* Front Page */
.noPadding {
  padding: 0;
}

.baner {
  margin-top: 10px;

  img {
    max-width: 100%;
    display: block;
    margin: 0 auto;
    height: auto;
  }

}

.zennosty {
  margin-top: 50px;
  p {
    font-family: "Century Gothic", sans-serif;
    font-size: 18px;
    color: #231f20;
    margin-top: 24px;
  }
}

.products {
  position: relative;

  &__title {
    font-family: "Century Gothic", sans-serif;
    font-size: 36px;
    color: #231f20;
    padding: 0 0 19px 23px;
    border-bottom: 1px solid #d1d3d4;
  }

  &__view {
    font-family: "Century Gothic", sans-serif;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 67px;
    height: 510px;

    &__img {
      height: 197px;
    }

    &__lable {
      position: absolute;
      left: 0;
      top: 20px;
      margin-left: 52px;
    }

    &__title {
      justify-content: center;
      margin-top: 30px;
      text-align: center;
      font-size: 18px;
      line-height: normal;
    }

    &__price {
      font-family: "Century Gothic", sans-serif;
      width: 53px;
      height: 19px;
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      margin-top: 16px;
      text-align: center;
      font-size: 14px;

      .summa {
        font-size: 18px;
        font-weight: bold;
      }
    }
  }
  .slider__wrapper {
    margin: 0 auto;
  }
}

.news {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  margin-top: 40px;

  img {
    display: block;
    font-family: "object-fit: cover; object-position: 50% 0%";
    -o-object-fit: cover;
    object-fit: cover;
    -o-object-position: 50% 0;
    object-position: 50% 0;
    height: 100%;
    width: 100%;
    overflow: hidden;
  }

  &__item {
    margin-right: 0;
    margin-left: 0;

    &:not(:last-child) {
      margin-bottom: 4px;
    }
  }

  &__single {
    position: relative;
    overflow: hidden;

    a{
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 3;
      display: block;
    }

    &-big {
      height: 654px;

      img{
        min-height: 654px;
      }
    }

    &-small {
      height: 325px;

      img{
        min-height: 325px;
      }

    }

    &-mr{
      margin-right: 4px;

      &.row{
        margin-left: 0;
      }
    }

    &-ml{
      margin-left: 4px;
    }

    &-bm{
      margin-bottom: 4px;
    }


    &:hover{
      .news__single-title{
        background-color: #f57f29;
      }
    }

    &-title {
      position: absolute;
      background: #929396;
      bottom: 0;
      left: 0;
      right: 0;
      min-height: 80px;
      text-align: left;
      padding: 0 20px;
      display: flex;
      flex-direction: column;
      align-self: flex-start;
      justify-content: center;
      color: #ffffff;
      font-family: "Century Gothic", sans-serif;
      font-size: 22px;
      line-height: normal;
      z-index: 2;
      transition: all 0.5s ease;
    }
  }
}
