.frontSlider {
  position: relative;

  &__media{
    outline: 0;
  }

  img {
    width: 100%;
  }

  .slick-arrow {
    position: absolute;
    background-color: rgb(188, 190, 192);
    width: 40px;
    height: 40px;
    top: 50%;
    margin-top: -20px;
    z-index: 20;
    border: 0;
    opacity: 0.8;
    transition: all 0.5s ease;
    font-size: 0;
    background-repeat: no-repeat;
    background-position: center;
    cursor: pointer;
    outline: 0;

    &:hover,&:focus {
      background-color: #f57f29;
      opacity: 1;
      outline: 0;
    }

    &.slick-prev {
      left: 0;
      background-image: url("data:image/svg+xml;base64,");
    }

    &.slick-next {
      right: 0;
      transform: rotate(180deg);
      background-image: url("data:image/svg+xml;base64,");
    }

  }

  .slick-dots {
    position: absolute;
    left: 0;
    right: 0;
    top: auto;
    bottom: 20px;

    padding: 0;
    margin: 0;
    list-style: none;
    text-align: center;

    li {
      display: inline-block;

      button {
        border-radius: 50%;
        background-color: rgb(188, 190, 192);
        width: 10px;
        height: 11px;
        outline: 0;
        font-size: 0;
        display: block;
        opacity: 0.8;
        transition: all 0.5s ease;
        cursor: pointer;
        border: 0;

        &:hover{
          background-color: rgb(0, 0, 0);
          opacity: 1;
          outline: 0;
        }

        &:focus{
          outline: 0;
        }

      }

      &.slick-active{
        button {
          background-color: rgb(0, 0, 0);
          opacity: 1;
          outline: 0;
        }
      }


      &:not(:last-child) {
        margin-right: 10px;
      }
    }
  }

}