.footer {
  margin-top: 100px;

  &__item {
    border-top: 1px solid #d1d3d4;
  }

  &__top {
    padding: 25px 20px;
  }

  &__middle {
    padding: 25px 0;

    .border-right {
      padding-right: 0;
      border-right: 1px solid #d1d3d4;
    }

    .border-left {
      padding-left: 0;
      border-left: 1px solid #d1d3d4;
    }
  }

  &__copyright {
    padding: 25px 0;
  }

}

.widget {

  &__title {
    font-size: 16px;
    font-family: "Century Gothic", sans-serif;
    color: #231f20;
    margin: 0 0 25px 0;
  }

  &_menu {
    ul {

      li {
        &:not(:last-child) {
          margin-bottom: 15px;
        }
      }
      a {
        font-size: 14px;
        font-family: "Century Gothic", sans-serif;
        color: #6d6e71;
        text-decoration: none;
        transition: all 0.5s ease;

        &:hover {
          color: #f57f29;
        }
      }
    }
  }

  &_payments {

    img {
      padding: 0 0 10px 0;
      vertical-align: middle;
    }

  }

  &_social {
    .socials {
      ul {
        li {
          &:not(:last-child) {
            margin-right: 15px;
            margin-bottom: 15px;
          }
          a {
            width: 40px;
            height: 40px;
            line-height: 40px;
            font-size: 22px;
          }
        }
      }
    }
  }

  &_contact {
    padding: 36px 20px;

    .address {
      font-size: 14px;
      font-family: "Century Gothic", sans-serif;
      color: rgb(109, 110, 113);
      line-height: 1.286;
      margin-bottom: 30px;
    }

    .contacts__data {
      margin-bottom: 30px;
      a {
        display: block;
        font-size: 22px;
        font-family: "Century Gothic", sans-serif;
        color: rgb(35, 31, 32);
        line-height: 1.455;
        text-decoration: none;
        transition: all 0.5s ease;

        &:hover {
          color: #f57f29;
        }
      }
    }
  }

  &_newsletter {
    padding: 36px 20px;

    p {
      margin-top: 0;
      margin-bottom: 50px;
      font-size: 14px;
      font-family: "Century Gothic", sans-serif;
      color: rgb(109, 110, 113);
      line-height: 1.286;
    }

    h2 {
      font-size: 22px;
      font-family: "Century Gothic", sans-serif;
      color: rgb(35, 31, 32);
      line-height: 1.273;
      margin-top: 0;
      margin-bottom: 35px;
    }

    .form__group {
      input {
        border-radius: 4px;
        border: 1px solid rgb(209, 211, 212);
        padding: 5px 10px;
        min-width: 220px;
      }

      button {
        border-radius: 4px;
        background-color: #f57f29;
        font-size: 14px;
        font-family: "Century Gothic", sans-serif;
        color: #fffefe;
        padding: 5px 10px;
        border: 1px solid #f57f29;
        margin-left: -10px;
      }
    }
  }
}